@import url(https://fonts.googleapis.com/css?family=Archivo+Black|Black+Ops+One|Bungee+Inline|Fredoka+One|Nosifer|Righteous|Rubik+Mono+One|Russo+One&display=swap);
*,
*::before,
*::after {
  margin: 0;
  padding: 0;
  box-sizing: border-box; }

body {
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
  min-width: 320px;
  height: 100vh;
  width: 100vw;
  background: linear-gradient(to right, #2d345c 0, #2d345c calc(50vw - 80px), black calc(50vw - 80px), black calc(50vw - 40px), blue calc(50vw - 40px), blue calc(50vw + 40px), black calc(50vw + 40px), black calc(50vw + 80px), #2d345c calc(50vw + 80px));
  background-color: #2d3456;
  font-family: "Archivo Black";
  color: #f3f2f1;
  overflow-x: hidden; }

.app {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  padding: env(safe-area-inset-top);
  width: 100vw;
  height: 100vh; }
  .app .header {
    display: flex;
    align-items: center;
    justify-content: space-between;
    z-index: 55; }
    .app .header h1 {
      margin-top: 2.5vh; }
  .app .data,
  .app .title {
    font-family: Black Ops One;
    font-size: calc(24px + (24 - 14) * ((100vw - 300px) / (1600 - 300)));
    text-align: center;
    padding-right: 1vw;
    text-shadow: 0 2px 5px rgba(0, 0, 0, 0.75); }

.title {
  z-index: 55; }

.data {
  z-index: 50; }

#viz {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  z-index: 50;
  width: 240px;
  height: 240px;
  position: relative; }
  #viz canvas {
    font-family: Archivo Black;
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0; }
  #viz .dial {
    position: absolute;
    top: -1%;
    left: -1%;
    bottom: 0;
    right: 0;
    display: flex;
    align-items: center;
    justify-content: center;
    width: 240px;
    height: 240px;
    border-radius: 50%;
    position: relative; }
    #viz .dial::after {
      content: "";
      position: absolute;
      top: 0;
      left: 0;
      width: 240px;
      height: 240px;
      border-radius: 50%; }
    #viz .dial--display {
      background: #000;
      width: 180px;
      height: 180px;
      border-radius: 50%;
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: center; }
      #viz .dial--display-text {
        font-family: Black Ops One;
        font-size: calc(100px + (100 - 90) * ((100vw - 300px) / (1600 - 300)));
        margin-top: -.25em;
        margin-bottom: -.3em; }

.button {
  border: 1px solid black;
  padding: 20px;
  max-width: 85vw;
  width: 85vw;
  text-align: center;
  margin: 1vh;
  border-radius: 10px;
  border-color: white;
  color: #f3f2f1;
  box-shadow: 2px 2px 5px rgba(0, 0, 0, 0.5); }

.start {
  background: blue;
  min-width: 5vw; }
  .start:active {
    background-color: darkblue; }

.reset {
  background: #d63031;
  margin-bottom: 2.5vh; }
  .reset:active {
    background-color: #801919; }

.timer--display {
  width: 85vw;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  min-height: 20vh; }
  .timer--display-num {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: .35em 1.5em;
    min-width: 130px;
    border: 1px solid white;
    border-radius: 15px;
    background-color: black;
    text-align: center; }
    .timer--display-num h6 {
      font-size: calc(8px + (8 - 4) * ((100vw - 300px) / (1600 - 300))); }
  .timer--display .fas {
    font-size: 1.25em; }
  .timer--display .button-fa {
    border-radius: 15px;
    border: 1px solid white;
    padding: 1em; }
    .timer--display .button-fa:active {
      -webkit-filter: invert(85%);
              filter: invert(85%); }
  .timer--display .fa-minus.button-fa {
    background-color: red; }
  .timer--display .fa-plus.button-fa {
    background-color: green; }

.center {
  justify-content: center; }

@media screen and (min-width: 540px) {
  .button {
    width: calc(540px * 0.85); }
  .timer--display {
    width: calc(540px * 0.85); } }

@media screen and (orientation: landscape) and (max-width: 960px) {
  .app {
    flex-direction: row;
    justify-content: space-between; }
  .title {
    width: 100vw; }
  .data,
  .timer--display {
    width: calc(33.3vw - 120px);
    max-width: calc(33.3vw - 120px);
    min-width: calc(33.3vw - 120px);
    padding: 1em; }
  .timer--display {
    flex-direction: column-reverse; }
    .timer--display-num {
      padding: .5em;
      max-width: 115px; }
    .timer--display .fas {
      margin: 1.5em; }
  .header,
  .reset {
    position: absolute;
    margin: 0 auto; }
  .header {
    top: 0;
    left: 0;
    right: 0;
    text-align: center; }
  .reset {
    bottom: 0;
    left: 0;
    right: 0; } }

