// font-size: calc([minimum size] + ([maximum size] - [minimum size]) * ((100vw - [minimum viewport width]) / ([maximum viewport width] - [minimum viewport width])));

@import url('https://fonts.googleapis.com/css?family=Archivo+Black|Black+Ops+One|Bungee+Inline|Fredoka+One|Nosifer|Righteous|Rubik+Mono+One|Russo+One&display=swap');

//Colors
$text-color: #f3f2f1;
$contrast-light: #a29490;
$bg-main: #2d345c;

//Fonts
$primary-font: Black Ops One;
$secondary-font: Archivo Black;

//Sizes for responsive font calc
$Dial-max: 130px;
$Dial-min: 95px;
$min-vw: 320px;
$max-vw: 1280px;

//Responsive fonts
$head-resp: calc(24px + (24 - 14) * ((100vw - 300px) / (1600 - 300)));
$dial-resp: calc(100px + (100 - 90) * ((100vw - 300px) / (1600 - 300)));
$inp-resp: calc(8px + (8 - 4) * ((100vw - 300px) / (1600 - 300)));

//Thin Blue Line Ribbon Gradient
$tbl-ol: calc(50vw - 80px);  //Ribbon outer left
$tbl-il: calc(50vw - 40px);  //Ribbon inner left
$tbl-ir: calc(50vw + 40px);  //Ribbon inner right
$tbl-or: calc(50vw + 80px);  //Ribbon outer right

//Global reset
*,
*::before,
*::after {
  margin: 0;
  padding: 0;
  box-sizing: border-box; }

//Global
body {
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
  min-width: 320px;
  height: 100vh;
  width: 100vw;
  background: linear-gradient(to right, $bg-main 0, $bg-main $tbl-ol, black $tbl-ol, black $tbl-il, blue $tbl-il, blue $tbl-ir, black $tbl-ir, black $tbl-or, $bg-main $tbl-or);
  background-color: #2d3456;
  font-family: "Archivo Black";
  color: $text-color;
  overflow-x: hidden; }

//App
.app {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  padding: env(safe-area-inset-top);

  width: 100vw;
  height: 100vh;

  .header {
    display: flex;
    align-items: center;
    justify-content: space-between;
    z-index: 55;
    h1 {
      margin-top: 2.5vh; } }

  .data,
  .title {
    font-family: $primary-font;
    font-size: $head-resp;//5vmin
    text-align: center;
    padding-right: 1vw;
    text-shadow: 0 2px 5px rgba(0, 0, 0, 0.75); } }

.title {
  z-index: 55; }


.data {
  z-index: 50; }

//Canvas and Text Elements
#viz {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  z-index: 50;
  width: 240px;
  height: 240px;
  position: relative;
  canvas {
    font-family: Archivo Black;
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0; }

  .dial {
    position: absolute;
    top: -1%;
    left: -1%;
    bottom: 0;
    right: 0;
    display: flex;
    align-items: center;
    justify-content: center;
    // background: #000
    width: 240px;
    height: 240px;
    border-radius: 50%;
    position: relative;
    &::after {
      content: "";
      position: absolute;
      top: 0;
      left: 0;
      width: 240px;
      height: 240px;
      border-radius: 50%;
 }      // border: 8px solid white
    &--display {
      background: #000;
      width: 180px;
      height: 180px;
      border-radius: 50%;
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: center;
      &-text {
        font-family: $primary-font;
        font-size: $dial-resp;
        margin-top: -.25em;
        margin-bottom: -.3em; } } } }

//Buttons
.button {
  border: 1px solid black;
  padding: 20px;
  max-width: 85vw;
  width: 85vw;
  text-align: center;
  margin: 1vh;
  border-radius: 10px;
  border-color: white;
  color: $text-color;
  box-shadow: 2px 2px 5px rgba(0, 0, 0, 0.5); }

.start {
  background: blue;
  min-width: 5vw;
  &:active {
    background-color: darkblue; } }

.reset {
  background: #d63031;
  margin-bottom: 2.5vh;
  &:active {
    background-color: #801919; } }

//Timer adjust controls
.timer--display {
  width: 85vw;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  min-height: 20vh;
  &-num {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: .35em 1.5em;
    min-width: 130px;
    border: 1px solid white;
    border-radius: 15px;
    background-color: black;
    text-align: center;
    h6 {
      font-size: $inp-resp; } }
  .fas {
    font-size: 1.25em; }

  .button-fa {
    border-radius: 15px;
    border: 1px solid white;
    padding: 1em;
    &:active {
      filter: invert(85%); } }

  .fa-minus.button-fa {
    background-color: red; }

  .fa-plus.button-fa {
    background-color: green; } }


.center {
  justify-content: center; }

//Non-mobile media queries
@media screen and (min-width: 540px) {

  .button {
    width: calc(540px * 0.85); }
  .timer--display {
    width: calc(540px * 0.85); } }


@media screen and (orientation: landscape) and (max-width: 960px) {
  .app {
    flex-direction: row;
    justify-content: space-between; }

  .title {
    width: 100vw; }

  .data,
  .timer--display {
    width: calc(33.3vw - 120px);
    max-width: calc(33.3vw - 120px);
    min-width: calc(33.3vw - 120px);
    padding: 1em; }

  .timer--display {
    flex-direction: column-reverse;

    &-num {
      padding: .5em;
      max-width: 115px; }

    .fas {
      margin: 1.5em; } }

  .header,
  .reset {
    position: absolute;
    margin: 0 auto; }

  .header {
    top: 0;
    left: 0;
    right: 0;
    text-align: center; }

  .reset {
    bottom: 0;
    left: 0;
    right: 0; } }
